@keyframes Hero_animation__1tDRN {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }

  20% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  80% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  90% {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
}

@-webkit-keyframes Hero_animation__1tDRN {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }

  20% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  80% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  90% {
    opacity: 0;
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
}

